* {
  margin: 0;
  padding: 0;
  outline: none;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  width: 100%;
}

html, body {
  height: 100%;
}

h1 {
  line-height: 1.5;
}

.navbar {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  padding: 1rem 2.5rem;
  z-index: 10;
  color: #FFFFFF;
  background: transparent;
}
.navbar-logo a {
  letter-spacing: 2.5;
  text-decoration: none;
  color: #FFFFFF;
}
.navbar-menu {
  width: 400px;
  display: flex;
  justify-content: space-between;
}
.navbar-menu a {
  color: #FFFFFF;
  text-decoration: none;
}

.menu-toggle {
  display: none;
}
.menu-toggle .bar {
  width: 15px;
  height: 2px;
  margin: 3px auto;
  transition: all 0.3 ease-in-out;
  background-color: #FFFFFF;
  cursor: pointer;
}

.header {
  width: 100%;
  height: 80%;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../../../../assets/img/banner.header.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.header-title {
  position: absolute;
  top: 7rem;
  left: 3rem;
  letter-spacing: 3;
  font-weight: bolder;
  font-size: 52px;
  color: #FFFFFF;
  text-shadow: 4px 2px 0px rgba(0, 0, 0, 0.2);
}
.header-title span {
  color: #0076DE;
}

.search-wrapper {
  width: 60%;
  margin: auto;
  min-width: 300px;
  height: 3.5rem;
  position: relative;
}

.search-box {
  width: 100%;
  position: absolute;
  background-color: #F6F8FA;
  box-shadow: rgba(2, 11, 20, 0.2) 0px 5px 15px;
  top: -45%;
  height: 100%;
  width: 100%;
}

.search-card {
  display: flex;
  justify-content: space-between;
  height: 100%;
  padding: 0.5rem;
}

.search-input {
  width: 100%;
  height: 100%;
  border: none;
  padding: 0.5rem;
}

.search-button {
  width: 200px;
  background-color: #0076DE;
  border: none;
  color: #FFFFFF;
  cursor: pointer;
}

.filter-box {
  height: auto;
  font-size: 0.8rem;
  width: 60%;
  min-width: 300px;
  margin: auto;
}

.filter-dropdown {
  width: 100%;
  border-bottom: 1px solid #64B1F5;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 0.3rem;
}

.select-filter {
  border: none;
  padding: 0.2rem;
}
.select-filter:hover {
  background-color: #F6F8FA;
}

.filter-chosen {
  width: 100%;
  padding: 0.3rem 0;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.chosen-card {
  background: #0076DE;
  padding: 0.2rem 0.5rem;
  border-radius: 20px;
  color: #FFFFFF;
  cursor: pointer;
}

.job-list {
  margin: 5%;
  padding: 1rem;
  background-color: #F6F8FA;
}

.job-card {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  margin: 1rem;
  min-height: 100px;
  align-items: center;
  background-color: #FFFFFF;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  cursor: pointer;
}
.job-card:hover {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
}

.job-name {
  display: flex;
}

.job-profile {
  width: 60px;
  height: 60px;
  margin: 0.5rem;
}

.job-detail {
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.job-detail > h4 {
  color: #0076DE;
  font-size: 0.9rem;
}
.job-detail > p {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.job-label > a {
  padding: 0 1rem;
  margin: 0.2rem;
  color: #FFFFFF;
  text-decoration: none;
  border-radius: 20px;
  font-size: 0.8rem;
}

.label-a {
  background-color: #FFD723;
}

.label-b {
  background-color: #0076DE;
}

.label-c {
  background-color: #333333;
}

.job-posted {
  font-size: 0.8rem;
}

.job-more {
  display: block;
  padding: 0.3rem 2rem;
  margin: 20px auto;
  width: 250px;
  background-color: transparent;
  color: #0076DE;
  border: 1px solid #0076DE;
  border-radius: 20px;
  cursor: pointer;
}
.job-more:hover {
  background-color: #0076DE;
  color: #FFFFFF;
}

.join {
  width: 100%;
  height: 350px;
  background-image: url("../../../../assets/img/coding-man.jpg");
  background-position: top right;
  background-repeat: no-repeat;
  background-size: cover;
  color: #FFFFFF;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 1rem 3rem;
  align-items: center;
  position: relative;
}
.join::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 85, 177, 0.6) 0%, rgba(0, 122, 255, 0.5) 100%), linear-gradient(180deg, rgba(0, 122, 255, 0.65) 0%, transparent 100%);
  z-index: 1;
}
.join-detail {
  min-width: 280px;
  width: 50%;
  z-index: 2;
  margin-bottom: 1rem;
}
.join-detail > h1 {
  font-size: 36px;
  letter-spacing: 2 !important;
}
.join-button {
  width: 200px;
  padding: 1rem;
  border: none;
  color: #333333;
  background-color: #FFD723;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  z-index: 2;
  cursor: pointer;
}
.join-button:hover {
  background: transparent;
  border: 2px solid #FFD723;
  color: #FFD723;
}

.featured {
  width: 100%;
  height: auto;
  text-align: center;
  padding: 5rem 1rem;
  background-color: #F6F8FA;
}
.featured .section-title {
  margin-bottom: 0.5rem;
  letter-spacing: 1.5;
  font-size: 36px;
}
.featured-image {
  width: 60px;
  height: 60px;
  margin-bottom: 1rem;
}
.featured-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 2rem;
  align-items: center;
  width: 100%;
  padding: 2% 12%;
}
.featured-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  background-color: #FFFFFF;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}
.featured-card:hover {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px;
}
.featured-button {
  border: 1px solid #0076DE;
  background-color: transparent;
  margin-top: 0.5rem;
  padding: 3px 10px;
  cursor: pointer;
  color: #0076DE;
}
.featured-button:hover {
  background-color: #0076DE;
  color: #FFFFFF;
}

.testimonials {
  width: 100%;
  text-align: center;
  padding: 5rem 1rem;
  background-color: #0076DE;
  color: #FFFFFF;
}

.swiper {
  width: 80%;
  height: 400px;
  margin-top: 2rem;
}
.swiper-slide {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2rem 0rem;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  color: #333333;
}
.swiper-slide i {
  font-size: 40px;
  color: #FFD723;
  margin-bottom: 1rem;
}
.swiper-slide h3 {
  font-family: "Noto Serif", serif;
  padding-inline: 1rem;
}

.testimoni-pic {
  padding: 1rem;
}
.testimoni-pic img {
  display: block;
  margin: 1rem auto;
  width: 50px;
  height: 50px;
  object-fit: cover;
  object-position: top;
  border-radius: 50%;
}

.blog {
  width: 100%;
  height: auto;
  text-align: center;
  padding: 5rem 1rem;
}
.blog-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  padding: 1rem;
}
.blog-card {
  width: 350px;
  height: auto;
  margin: 1rem;
  box-shadow: 0 1px 6px 0 rgba(49, 53, 59, 0.12);
  cursor: pointer;
}
.blog-card:hover {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.blog-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}
.blog-detail {
  padding: 20px;
}
.blog-detail span {
  color: #FFD723;
  font-size: 0.7rem;
}
.blog-detail h4 {
  padding: 10px 0;
}
.blog-more {
  color: #0076DE;
  text-decoration: none;
  font-size: 0.9rem;
}

.divider {
  margin: 1rem 0;
  border: 0.5px solid #F6F8FA;
}

.footer {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #0076DE;
  color: #FFFFFF;
  padding: 3rem;
}
.footer-wrapper {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  justify-content: flex-start;
  align-items: flex-end;
}
.footer-wrapper h3,
.footer-wrapper h4 {
  line-height: 3;
  color: #FFD723;
}
.footer-wrapper p {
  max-width: 400px;
  line-height: 1.5;
}
.footer-wrapper a {
  text-decoration: none;
  color: #F6F8FA;
  line-height: 2;
}
.footer-wrapper a:hover {
  text-decoration: underline;
}

.social-media {
  margin-top: 1rem;
}
.social-media a {
  text-decoration: none;
  color: #F6F8FA;
  margin-right: 10px;
}

@media screen and (max-width: 700px) {
  .join-detail {
    min-width: auto;
    width: auto;
  }
  .join-detail h1 {
    font-size: 32px;
  }
  .join-detail .section-none {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  .featured-image {
    width: auto;
    height: 30px;
    margin-bottom: 0.2rem;
  }
  .featured-wrapper {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    padding: 5% 10%;
  }
  .featured-card {
    padding: 1rem;
  }
  .menu-toggle,
  .bar {
    display: block;
    z-index: 3;
  }
  .menu-toggle.is-active .bar:nth-child(2) {
    opacity: 0;
  }
  .menu-toggle.is-active .bar:nth-child(1) {
    transform: translateY(5px) rotate(45deg);
  }
  .menu-toggle.is-active .bar:nth-child(3) {
    transform: translateY(-5px) rotate(-45deg);
  }
  .navbar-menu {
    flex-direction: column;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    text-align: center;
    background-color: #333333;
    display: none;
    padding: 5rem;
  }
  .navbar-menu a {
    color: #FFFFFF;
    padding: 1.5rem;
  }
  .navbar-menu.active {
    display: flex;
  }
  .header-title {
    font-size: 38px;
  }
  .job-label {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  .job-posted {
    display: none;
  }
}
@media screen and (max-width: 320px) {
  .join-detail h1 {
    font-size: 25px;
  }
  .join-detail p {
    font-size: 14px;
  }
  .join-detail .section-none {
    display: none;
  }
}